// Test-only utilities (not used in production runtime)
import * as fs from 'fs';
import * as path from 'path';

export interface DataFile {
  filePath: string;
  fileName: string;
  raw: string;
  parsed?: unknown;
  error?: string;
}

export interface PairCase {
  id: string;                        // numeric id captured from filename (_<id>.json)
  sourceFile: DataFile;
  destinationFile: DataFile;
  sourcePayload: unknown;
  destinationPayload: unknown;
}

const JSON_EXT = '.json';
const pairRegex = /_(\d+)\.json$/i;   // <-- pairing regex

/**
 * Reads all files under: baseDataFolder / integrationId / positive
 * Returns metadata plus parsed JSON (if .json).
 * Intended for test usage only.
 */
export function readDataFilesByType(
  baseDataFolder: string,
  integrationId: string,
  type: string //positive or negative
): DataFile[] {
  const dir = path.join(baseDataFolder, integrationId, type);
  if (!fs.existsSync(dir) || !fs.statSync(dir).isDirectory()) return [];

  const results: DataFile[] = [];

  for (const entry of fs.readdirSync(dir, { withFileTypes: true })) {
    if (!entry.isFile()) continue;
    const fileName = entry.name;
    const filePath = path.join(dir, fileName);
    try {
      const raw = fs.readFileSync(filePath, 'utf8');
      if (fileName.toLowerCase().endsWith(JSON_EXT)) {
        try {
          results.push({ filePath, fileName, raw, parsed: JSON.parse(raw) });
        } catch (e: any) {
          results.push({ filePath, fileName, raw, error: `JSON parse error: ${e.message}` });
        }
      } else {
        results.push({ filePath, fileName, raw });
      }
    } catch (e: any) {
      results.push({ filePath, fileName, raw: '', error: `Read error: ${e.message}` });
    }
  }
  return results;
}

export function mergeInt31Files(files: any[]) {
  let headerPayload: any = {};
  let detailPayload: any = {};
  let mainFile: any = {};
  let fileSuccess: string = "1";

  for (const file of files) {
    const { payload, success } = JSON.parse(file.content);
    if (payload?.propertyCode || payload?.folioNumber || payload?.creationTS) {
      mainFile = file;
      headerPayload = payload;
    }
    if (payload?.folioTransDetailList) {
      detailPayload = payload;
      
    }
    success == "0" && (fileSuccess = "0");
  }

  const mergedPayload = { ...headerPayload, ...detailPayload };
  const content = JSON.stringify({success: fileSuccess, payload: mergedPayload });
  mainFile = { ...mainFile, content};
  return mainFile;
}

/**
 * Builds positive test cases by pairing source & destination JSON files whose
 * filenames both match /_(\\d+)\\.json$/i with the SAME captured number.
 * Example pairing:
 *   source: reservation_001.json  & destination: booking_001.json  => id "001"
 *
 * Files that do not match the regex or fail JSON parse are ignored.
 * If duplicates exist for the same id, the first encountered is used.
 */
export function buildPairCases(
  baseDataFolder: string,
  sourceIntegrationId: string,
  destinationIntegrationId: string, 
  type: string
): PairCase[] {
  const srcFiles = readDataFilesByType(baseDataFolder, sourceIntegrationId, type)
    .filter(f => f.parsed && !f.error && pairRegex.test(f.fileName));
  const destFiles = readDataFilesByType(baseDataFolder, destinationIntegrationId, type)
    .filter(f => f.parsed && !f.error && pairRegex.test(f.fileName));

  const indexById = (files: DataFile[], type:string) => {
    const map: Record<string, DataFile> = {};
    const int31Pairs: Record<string, DataFile[]> = {};
    if(destinationIntegrationId === "int31" && type==='dest'){
      // let m;
      for (const f of files) {
        const m = f.fileName.match(pairRegex);
        if (!m) continue;
        const id = m[1]; // numeric string
        if (!int31Pairs[id]) {
          int31Pairs[id] = [f];
        } else {
          int31Pairs[id].push(f);
        }
      }
        for(const id in int31Pairs){
          const files = int31Pairs[id];
          const merged = mergeInt31Files(files);
          map[id] = merged;
        }
      return map;
    } else {
      for (const f of files) {
        const m = f.fileName.match(pairRegex);
        if (!m) continue;
        const id = m[1]; // numeric string
        if (!map[id]) map[id] = f; // keep first (deterministic)
      }
      return map;
    }
  }
  
  const srcMap = indexById(srcFiles, "src");
  const destMap = indexById(destFiles, "dest");
  const commonIds = Object.keys(srcMap).filter(id => destMap[id]).sort((a, b) => Number(a) - Number(b));
  return commonIds.map(id => {    
    return {
      id,
      sourceFile: srcMap[id],
      destinationFile: destMap[id],
      sourcePayload: srcMap[id].parsed,
      destinationPayload: destMap[id].parsed
    };
  });
}

export function buildSingleCase(
  baseDataFolder: string,
  integrationId: string,
  type: string
): any[] {
  const files = readDataFilesByType(baseDataFolder, integrationId, type)
    .filter(f => f.parsed && !f.error && pairRegex.test(f.fileName));

  const indexById = (files: DataFile[]) => {
    const map: Record<string, DataFile> = {};
    for (const f of files) {
      const m = f.fileName.match(pairRegex);
      if (!m) continue;
      const id = m[1]; // numeric string
      if (!map[id]) map[id] = f; // keep first (deterministic)
    }
    return map;
  };
  const fileMap = indexById(files);
  return Object.keys(fileMap).map(id => {    
    return {
      id,
      file: fileMap[id],
      payload: fileMap[id].parsed,
    };
  }); 
}