import { REGEX } from '../common/regex-constants.js';
import type { RuleDefinition, RuleMap } from '../common/integration-validation.types.js';

export const int1531FieldRegexMapData = {
    "confirmationIds<array>.value": {regex: REGEX.ALPHANUMERIC, optional:true, overrides: [{ expr: 'confirmationIds<array>.provider=="ACRS"', set: { required: true } }] },
    "propertyCode": {regex: REGEX.ALPHANUMERIC, optional: true},
    "guestInformation.altCustId": {regex: REGEX.ALPHANUMERIC, optional: true}
} satisfies RuleMap;


export type Int1531FieldKeys = keyof typeof int1531FieldRegexMapData;

export const INT1531FieldRegexMap: Record<Int1531FieldKeys, RuleDefinition> = int1531FieldRegexMapData;